:root {
    --font-dm-serif-display: "DM Serif Display", serif;
    --font-dm-mono-display: "DM Mono", serif;
    --font-kalam-display: "Kalam", cursive;
    --color-brown-palette: #432F28;
    --color-dark-pink-palette: #fc8284;
    --color-light-pink-palette: #F9B1B2;
    --color-lighter-pink-palette: #ffddde;
    --color-dark-blue-palette: #1e36a1;
    --color-light-blue-palette: #D9D9FF;
    --color-lighter-blue-palette: #e9e9fc;
    --box-shadow-standard: 2px 0 4px rgba(0, 0, 0, 0.4);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
    color: var(--color-brown-palette);
}

body {
    position: relative;
}

a{
    text-decoration: none;
    color: var(--color-brown-palette);
}

li {
    list-style: none;
}

.center-margin {
    height: max-content;
    width: 93%;
    margin: 0 auto;
}

.top-bar {
    padding: 15px 0;
    display: flex;
    /* gap: 30px; */
    background-color: var(--color-dark-blue-palette);
}

.top-bar div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.top-bar p,
.top-bar a {
    color: #fff;
}

.top-bar-symbol {
    color: #fff;
    font-size: 10px;
}

.top-bar p {
    padding-left: 8px;
}

.top-bar a {
    padding-right: 8px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
    font-weight: 400;
}

.titles {
    font-family: var(--font-dm-serif-display);
    font-weight: normal;
    color: var(--color-brown-palette);
    font-size: 1.8rem;
}

.title-size {
    font-size: 2.1rem;
}

.lines {
    border-width: 0 0 1px 0;
    border-style: solid;
    color: var(--color-brown-palette);
}

.top-bar a:hover,
header h1 a:hover {
    text-decoration: underline;
}

.mid-bar {
    margin-top: 20px;
    height: 140px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
}

.mid-bar-menu {
    display: flex;
    justify-content: center;
}

.mid-bar-bottom {
    display: none;
    justify-content: end;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;
}

.mid-bar-bottom img {
    margin: 0 3px;
    width: 25px;
}

.mid-bar-bottom div {
    padding: 10px 10px 5px;
    /* background-color: var(--color-dark-pink-palette); */
    border-radius: 10px;
}

.mid-bar-h1 h1,
.mid-bar-h1 h1 a {
    min-height: max-content;
}

.logo {
    min-width: 100px;
    max-width: 150px;
    /* aspect-ratio: 1 / 1; */
}

.btn-shop {
    padding: 10px 15px;
    background-color: var(--color-light-pink-palette);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
    font-weight: 100;
    border-radius: 20px;
    border: 1px solid black;
    margin-left: 20px;
}   

.menu-mobile button {
    padding: 15px 0;
}

.large-menu {
    display: none;
    margin-top: 15px;
    flex-flow: column wrap;
    height: 100vh;
}

.btn-menu {
    border: none;
    /* background-color: #fff; */
    font-size: 1.7em;
    cursor: pointer;
}

.btn-space{
    margin: 10px 0;
}

.products-div {
    vertical-align: top;
    display: flex;
}

.products-page:hover {
    cursor: pointer;
}

@keyframes slidein {
    from {
        margin-left: 100%;
        width: 300%;
        max-width: 100%;
    }

    to {
        margin-left: 0%;
        width: 100%;
    }
}

.container {
    margin-top: 30px;
}

.first-article-img {
    width: 75%;
    margin: 0 auto;
}

.dm-mono {
    font-family: var(--font-dm-mono-display);
    font-weight: 100;
}

.dm-mono p a,
.img-grid .dm-mono p {
    font-family: var(--font-dm-mono-display);
    font-weight: 100;
}

.dm-mono p a:hover {
    text-decoration: underline;
    font-weight: normal;
}

.subtitle {
    font-size: 0.9em;
}

.article-title {
    width: max-content;
    font-size: 2.2em;
    background: linear-gradient(180deg, #fff 0%, #fff 50%, var(--color-lighter-blue-palette) 50%, var(--color-light-blue-palette) 100%);
}

.img-container {
    display: flex;
    flex-flow: column nowrap;
    margin: 25px 0;
    gap: 1.75rem;
}

.img-container img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    aspect-ratio: 1 / 1;
}

.img-grid {
    display: grid;
    /* box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.345); */
}

.img-grid img {
    grid-column: 1;
    grid-row: 1 / 3;
    align-self: end;
}

.img-grid div {
    background-color: #fff;
    grid-column: 1;
    grid-row: 2 / 3;
    align-self: end;
    width: 95%;
    padding: 15px;
    margin: 10px auto;
}

.img-grid div p {
    font-family: var(--font-dm-mono-display);
    font-weight: 500;
    font-size: 0.85em;
}

.img-grid div h2 {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
    font-size: 1.1em;
    font-weight: 500;
}

.form-container {
    margin: 50px 0;
}

.form-container h2 {
    font-size: 2.5rem;
    padding-top: 30px;
}

.form-container div {
    padding-top: 30px;
    padding-bottom: 30px;
}

.label-email {
    display: none;
}

.email {
    padding: 15px;
    font-size: 1em;
    border-radius: 30px;
    border: 1px solid black;
}

.img-container-posts {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 25px 0;
    gap: 1.75rem;
}

.img-container-posts div a img {
    aspect-ratio: 1 / 1;
    width: 100%;
    object-fit: cover;
}

.img-posts div {
    width: 100%;
    margin: 0;
    padding-left: 2px;
}

.description-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
}

.description-container div p {
    font-size: 0.8em;
}

.description-container div h2 {
    font-size: 1.8em;
}

.img-description {
    height: 80px;
    width: 80px;
    object-fit: cover;
    border: 1px solid black;
    border-radius: 50%;
}

.description-text {
    font-size: 0.9em;
    font-weight: 100;
    padding: 20px 0;
    line-height: 2em;
}

.description-article, 
.connect-medias {
    margin-top: 20px;
    margin-bottom: 20px;
}

.connect-medias {
    background-color: var(--color-lighter-blue-palette);
    padding: 10px;
    border-radius: 10px;
}

.social-medias {
    margin: 15px 0;
    display: flex;
}

.media-flex {
    display: flex;
    align-items: center;
    gap: 10px;
}

.description-socials {
    height: 30px;

}

.customer-article {
    margin-top: 2em;
    background-color: var(--color-lighter-blue-palette);
    padding: 1em;
}

.customer-article h2 {
    text-align: center;
}

.customer-div {
    display: flex;
    flex-flow: column nowrap;
    margin-top: 20px;
    gap: 30px;
}

.customer-div p {
    text-indent: 20px;
}

.customer-div h3 {
    font-size: 14px;
    text-align: right;
}

.customer-review {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    width: 60%;
    margin: 0 auto;
}

.div-review {
    display: block;
    width: 100%;
    text-align: right;
    padding-right: 16px;
}

.svg-quote {
    width: 15px;
}

.span-svg-close {
    display: flex;
    justify-content: right;
}

.digital-menu-article {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    margin-top: 20px;
    padding: 10px;
    gap: 10px;
    background-color: var(--color-dark-pink-palette);
    border-radius: 10px;
}

.digital-menu-div {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    padding: 10px;
    gap: 10px;
}

.digital-menu-text h2 {
    font-family: var(--font-dm-serif-display);
    font-size: 2em;
    font-weight: 400;
    color: #fff;
    text-align: center;
}

.digital-menu-text p {
    padding-top: 20px;
    color: #fff;
    text-align: center;
    width: 80%;
    margin: 0 auto;
}

.digital-menu-text strong {
    color: #fff;
}

.menu-img {
    margin-top: 30px;
}

.menu-img img {
    width: 300px;
}

.post-legend {
    font-weight: 500;
    font-size: 0.9rem;
}

.footer {
    background-color: #fdf1cd;
}

.footer-content {
    padding: 25px 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-around;
    /* align-items: center; */
    gap: 20px;
}

.footer-content {
    text-align: center;
}

.footer-options {
    display: flex;
    justify-content: center;
    gap: 30px;
    font-size: 0.9rem;
    font-weight: 100;
    padding: 3px 0;
}

.footer-contacts h2 {
    margin-bottom: 10px;
}

.footer-contacts p {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-brown-palette); 
}

.footer-icons {
    color: var(--color-brown-palette);  
}

.footer-midias {
    display: flex;
    justify-content: center;
    gap: 10px;
}

.footer-midias img {
    width: 25px;
}

.bottom-footer {
    margin: 15px 0;
    font-size: 0.8rem;
    font-weight: 100;
}

.bottom-footer a {
    text-decoration: underline;
}

.grid-article {
    display: grid;
    grid-template-columns: repeat(1fr, 3);
}

.symbols-white {
    color: white;
    font-size: 3em;
}

.info-article div {
    text-align: center;
    padding: 1.5em 0;
    width: 60%;
}

.info-article div p {
    color: #fff;
    font-size: 1.2em;
    font-weight: 200;
    padding: 10px;
}

.info-article div h3 {
    color: #fff;
    font-size: 1.5em;
    font-weight: 500;
    font-family: "Kalam", cursive;
}

.info-article {
    display: flex;
    height: max-content;
    justify-content: center;
    align-items: center;
    flex-flow: column nowrap;
    margin-top: 30px;
    background-color: var(--color-dark-pink-palette);
    border-radius: 10px;
}

/* GRID CONFIG */

.grid-main {
    display: grid;
    gap: 30px;
}


.first-article {
    grid-row: 1;
}

.digital-menu-article {
    grid-row: 2;
}

.info-article {
    grid-row: 4
}

.grid-large-size {
    grid-row: 3;
}

