\r\nli {\r\n    list-style: none;\r\n}\r\n\r\n.center-margin {\r\n    height: max-content;\r\n    width: 93%;\r\n    margin: 0 auto;\r\n}\r\n\r\n.top-bar {\r\n    padding: 15px 0;\r\n    display: flex;\r\n    /* gap: 30px; */\r\n    background-color: var(--color-dark-blue-palette);\r\n}\r\n\r\n.top-bar div {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.top-bar p,\r\n.top-bar a {\r\n    color: #fff;\r\n}\r\n\r\n.top-bar-symbol {\r\n    color: #fff;\r\n    font-size: 10px;\r\n}\r\n\r\n.top-bar p {\r\n    padding-left: 8px;\r\n}\r\n\r\n.top-bar a {\r\n    padding-right: 8px;\r\n    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;\r\n    font-weight: 400;\r\n}\r\n\r\n.titles {\r\n    font-family: var(--font-dm-serif-display);\r\n    font-weight: normal;\r\n    color: var(--color-brown-palette);\r\n    font-size: 1.8rem;\r\n}\r\n\r\n.title-size {\r\n    font-size: 2.1rem;\r\n}\r\n\r\n.lines {\r\n    border-width: 0 0 1px 0;\r\n    border-style: solid;\r\n    color: var(--color-brown-palette);\r\n}\r\n\r\n.top-bar a:hover,\r\nheader h1 a:hover {\r\n    text-decoration: underline;\r\n}\r\n\r\n.mid-bar {\r\n    margin-top: 20px;\r\n    height: 140px;\r\n    display: flex;\r\n    flex-flow: column nowrap;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n.mid-bar-menu {\r\n    display: flex;\r\n    justify-content: center;\r\n}\r\n\r\n.mid-bar-bottom {\r\n    display: none;\r\n    justify-content: end;\r\n    align-items: center;\r\n    gap: 20px;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.mid-bar-bottom img {\r\n    margin: 0 3px;\r\n    width: 25px;\r\n}\r\n\r\n.mid-bar-bottom div {\r\n    padding: 10px 10px 5px;\r\n    /* background-color: var(--color-dark-pink-palette); */\r\n    border-radius: 10px;\r\n}\r\n\r\n.mid-bar-h1 h1,\r\n.mid-bar-h1 h1 a {\r\n    min-height: max-content;\r\n}\r\n\r\n.logo {\r\n    min-width: 100px;\r\n    max-width: 150px;\r\n    /* aspect-ratio: 1 / 1; */\r\n}\r\n\r\n.btn-shop {\r\n    padding: 10px 15px;\r\n    background-color: var(--color-light-pink-palette);\r\n    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;\r\n    font-weight: 100;\r\n    border-radius: 20px;\r\n    border: 1px solid black;\r\n    margin-left: 20px;\r\n}   \r\n\r\n.menu-mobile button {\r\n    padding: 15px 0;\r\n}\r\n\r\n.large-menu {\r\n    display: none;\r\n    margin-top: 15px;\r\n    flex-flow: column wrap;\r\n    height: 100vh;\r\n}\r\n\r\n.btn-menu {\r\n    border: none;\r\n    /* background-color: #fff; */\r\n    font-size: 1.7em;\r\n    cursor: pointer;\r\n}\r\n\r\n.btn-space{\r\n    margin: 10px 0;\r\n}\r\n\r\n.products-div {\r\n    vertical-align: top;\r\n    display: flex;\r\n}\r\n\r\n.products-page:hover {\r\n    cursor: pointer;\r\n}\r\n\r\n@keyframes slidein {\r\n    from {\r\n        margin-left: 100%;\r\n        width: 300%;\r\n        max-width: 100%;\r\n    }\r\n\r\n    to {\r\n        margin-left: 0%;\r\n        width: 100%;\r\n    }\r\n}\r\n\r\n.container {\r\n    margin-top: 30px;\r\n}\r\n\r\n.first-article-img {\r\n    width: 75%;\r\n    margin: 0 auto;\r\n}\r\n\r\n.dm-mono {\r\n    font-family: var(--font-dm-mono-display);\r\n    font-weight: 100;\r\n}\r\n\r\n.dm-mono p a,\r\n.img-grid .dm-mono p {\r\n    font-family: var(--font-dm-mono-display);\r\n    font-weight: 100;\r\n}\r\n\r\n.dm-mono p a:hover {\r\n    text-decoration: underline;\r\n    font-weight: normal;\r\n}\r\n\r\n.subtitle {\r\n    font-size: 0.9em;\r\n}\r\n\r\n.article-title {\r\n    width: max-content;\r\n    font-size: 2.2em;\r\n    background: linear-gradient(180deg, #fff 0%, #fff 50%, var(--color-lighter-blue-palette) 50%, var(--color-light-blue-palette) 100%);\r\n}\r\n\r\n.img-container {\r\n    display: flex;\r\n    flex-flow: column nowrap;\r\n    margin: 25px 0;\r\n    gap: 1.75rem;\r\n}\r\n\r\n.im