___CSS_LOADER_EXPORT___.push([module.id, `.social-bar {
    display: flex;
    justify-content: end;
    align-items: center;
    flex-flow: column nowrap;
    gap: 10px;
    width: max-content;
    height: 100%;
    position: fixed;
    right: 10px;
    bottom: 10px;
    transition: .2s;
    padding: 5px;
    margin: auto 0;
    z-index: 99;
}

.btn-whatsapp {
    background-color: #25D366;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.24);
    border-radius: 10px;
    cursor: pointer;
}

.btn-whatsapp img {
    width: 50px;
}

@media (min-width: 600px) {
    .btn-whatsapp img {
        width: 60px;
        transition: transform 700ms;
    }

    .btn-whatsapp:hover {
        transform: scale(1.1);
    }
