___CSS_LOADER_EXPORT___.push([module.id, `@charset "utf-8";

@media (min-width: 600px) {

    .mid-bar-menu {
        flex-flow: row nowrap;
    }

    .mid-bar-bottom img:hover {
        transform: translate(-3px, -3px);
        transition: transform 0.3s, border .5s;
    }

    .menu-mobile {
        display: none !important;
    }

    .large-menu {
        display: flex !important;
        height: max-content;
        flex-flow: row nowrap;
        margin: 15px 0;
    }

    .options-menu {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-around;
        height: max-content;
        gap: 10px;
        height: auto;
    }

    .options-menu .products-list li {
        margin-top: 10px;
        list-style: none;
        font-size: 0.8em;
    }

    .options-menu li {
        margin-top: 0;
        padding: 5px;
        font-family: var(--font-dm-mono-display);
        text-transform: uppercase;
    }

    .options-menu li:hover {
        background-color: var(--color-lighter-blue-palette);
    }

    .first-article-img {
        width: 100%;
    }

    .img-container-posts>div {
        height: 33.33%;
    }

    .img-container-posts div a img {
        height: 400px;
    }

    .img-container {
        display: grid !important;
        grid-template-columns: repeat(3, 1fr);
    }

    .img-container .img-grid {
        position: relative;
    }

    .img-container .img-grid div {
        display: none;
        width: 80%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%)
    }

    .img-container .img-grid:hover div {
        display: inline-block;
        animation: position .1s linear;

        bottom: 0;
    }

    @keyframes position {
        0% {
            bottom: -40px;
        }

        50% {
            bottom: -20px;
        }

        100% {
            bottom: 0px;
        }
    }

    .img-grid div a:hover {
        text-decoration: underline;
    }

    .media-flex:hover {
        /* -moz-transform: scale(1.1); */
        transform: scale(1.1);
    }

    .bottom-article-img {
        grid-template-columns: repeat(2, 1fr);
        /* grid-template-rows: repeat(3, 300px); */
        gap: 1.75rem;
    }

    .bottom-article-img a {
        height: 100%;
    }

    .bottom-article-img div a img {
        aspect-ratio: 1 / 1;
        height: calc(100% - 25px); 
        width: 100%;
    }

    .img-container-posts {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .dropdown-menu {
        display: none;
        position: absolute;
        background-color: #fff;
        padding: 0 10px 10px 10px;
        box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.418);
        z-index: 999;
    }

    .dropdown-menu a {
        display: block;
    }

    .dropdown:hover .dropdown-menu{
        display: block !important;
    }

    .products-list {
        display: block !important;
        line-height: 1.5em;
        margin: 0;
    } 
    
    .post-legend:hover {
        text-decoration: underline;
    }


    .bottom-footer a:hover {
        text-decoration: none;
    }

    
    .footer a:hover {
        text-decoration: underline;
    }

    .footer-midias img:hover {
        transform: scale(1.1);
    }

    .digital-menu-article {
        justify-content: center;
        align-items: start;
    }

    .digital-menu-div {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        width: 100%;
    }

    .digital-menu-text {
        padding: 30px;
        padding-right: 15px;
        width: 100%;
        margin: 0 auto;
    }
    
    .digital-menu-text h2 {
        text-align: start;
    }
    
    .digital-menu-text p {
        padding-top: 20px;
        text-align: start;
        margin: 0;
        line-height: 1.7em;
        font-size: 1.1em;
    }
    
    .menu-img {
        margin-top: 0;
    }
    
    .menu-img img {
        aspect-ratio: 1 / 1;
        width: 100%;
        max-width: 400px;
        margin-top: 10px;
        transition: transform 0.3s;
    }

    .digital-menu-div:hover .menu-img img {
        transform: scale(0.87) rotateZ(15deg);
    }

    .img-posts {
        transition: transform 0.15s;
    }

    .img-posts:hover {
        transform: scale(1.1);
    }

    .digital-menu-h2 {
        background-image: linear-gradient(var(--color-brown-palette), var(--color-brown-palette));
        background-repeat: no-repeat;
        background-size: 0% 3px;
        background-position: left bottom;
        width: max-content;
        transition: background-size 500ms ease;
    }

    .digital-menu-article div:nth-child(2) .digital-menu-h2 {
        background-position: right bottom;
    }

    .digital-menu-div:hover .digital-menu-h2 {
        background-size: 100% 3px;
    }

    .top-bar p,
    .top-bar a,
    .top-bar-symbol {
        font-size: 16px;
    }

}`, "",{"version":3,"sources":["webpack://./frontend/assets/css/medium-size.css"],"names":[],"mappings":"AAAA,gBAAgB;;AAEhB;;IAEI;QACI,qBAAqB;IACzB;;IAEA;QACI,gCAAgC;QAChC,sCAAsC;IAC1C;;IAEA;QACI,wBAAwB;IAC5B;;IAEA;QACI,wBAAwB;QACxB,mBAAmB;QACnB,qBAAqB;QACrB,cAAc;IAClB;;IAEA;QACI,aAAa;QACb,mBAAmB;QACnB,6BAA6B;QAC7B,mBAAmB;QACnB,SAAS;QACT,YAAY;IAChB;;IAEA;QACI,gBAAgB;QAChB,gBAAgB;QAChB,gBAAgB;IACpB;;IAEA;QACI,aAAa;QACb,YAAY;QACZ,wCAAwC;QACxC,yBAAyB;IAC7B;;IAEA;QACI,mDAAmD;IACvD;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,wBAAwB;QACxB,qCAAqC;IACzC;;IAEA;QACI,kBAAkB;IACtB;;IAEA;QACI,aAAa;QACb,UAAU;QACV,kBAAkB;QAClB,SAAS;QACT;IACJ;;IAEA;QACI,qBAAqB;QACrB,8BAA8B;;QAE9B,SAAS;IACb;;IAEA;QACI;YACI,aAAa;QACjB;;QAEA;YACI,aAAa;QACjB;;QAEA;YACI,WAAW;QACf;IACJ;;IAEA;QACI,0BAA0B;IAC9B;;IAEA;QACI,gCAAgC;QAChC,qBAAqB;IACzB;;IAEA;QACI,qCAAqC;QACrC,0CAA0C;QAC1C,YAAY;IAChB;;IAEA;QACI,YAAY;IAChB;;IAEA;QACI,mBAAmB;QACnB,yBAAyB;QACzB,WAAW;IACf;;IAEA;QACI,aAAa;QACb,qCAAqC;IACzC;;IAEA;QACI,aAAa;QACb,kBAAkB;QAClB,sBAAsB;QACtB,yBAAyB;QACzB,4CAA4C;QAC5C,YAAY;IAChB;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,yBAAyB;IAC7B;;IAEA;QACI,yBAAyB;QACzB,kBAAkB;QAClB,SAAS;IACb;;IAEA;QACI,0BAA0B;IAC9B;;;IAGA;QACI,qBAAqB;IACzB;;;IAGA;QACI,0BAA0B;IAC9B;;IAEA;QACI,qBAAqB;IACzB;;IAEA;QACI,uBAAuB;QACvB,kBAAkB;IACtB;;IAEA;QACI,aAAa;QACb,qCAAqC;QACrC,WAAW;IACf;;IAEA;QACI,aAAa;QACb,mBAAmB;QACnB,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,iBAAiB;QACjB,iBAAiB;QACjB,SAAS;QACT,kBAAkB;QAClB,gBAAgB;IACpB;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,mBAAmB;QACnB,WAAW;QACX,gBAAgB;QAChB,gBAAgB;QAChB,0BAA0B;IAC9B;;IAEA;QACI,qCAAqC;IACzC;;IAEA;QACI,2BAA2B;IAC/B;;IAEA;QACI,qBAAqB;IACzB;;IAEA;QACI,yFAAyF;QACzF,4BAA4B;QAC5B,uBAAuB;QACvB,gCAAgC;QAChC,kBAAkB;QAClB,sCAAsC;IAC1C;;IAEA;QACI,iCAAiC;IACrC;;IAEA;QACI,yBAAyB;IAC7B;;IAEA;;;QAGI,eAAe;IACnB;;AAEJ","sourcesContent":["@charset \"utf-8\";\r\n\r\n@media (min-width: 600px) {\r\n\r\n    .mid-bar-menu {\r\n        flex-flow: row nowrap;\r\n    }\r\n\r\n    