    height: 500px;
    background: center center no-repeat;
    background-size: cover;
    padding: 20px 120px;
}

.inside-background {
    background-color: #ffffff;
    padding-top: 1px;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.22);
    margin: 0 auto;
    border-radius: 20px;
    min-width: 150px;
}

@media (min-width: 600px) {
    .background-menu {
        padding: 20px 70px;
    }

    .inside-background {
        max-width: 445px;
    }
}

