/* @media (min-width: ) {
    .img-container-posts {
        grid-template-columns: repeat(3, 1fr) !important; 
    }
} */

@media (min-width: 905px) {
    .center-margin {
        max-width: 1500px;
    }

    .mid-bar {
        margin-top: 0px;
        height: 70px;
        display: flex;
        flex-flow: row nowrap !important;
        justify-content: space-between;
        align-items: center;
        gap: 70px;
    }

    .mid-bar-menu {
        justify-content: start;
    }

    .large-menu {
        height: auto;
    }

    .form-flex {
        display: flex;
        align-items: center;
    }

    .form-flex h2 {
        padding: 0;
        width: 100%;
    }

    .grid-large-size {
        height: max-content;
        display: grid;
        gap: 2rem;
    }

    .grid-large-size article:nth-child(1) {
        grid-column-start: 1;
        grid-column-end: 3;

        height: max-content;
    }


    .img-container-posts {
        grid-template-columns: repeat(4, 1fr);
    }


    .img-container-posts>div {
        height: 100%;
    }

    .img-container-posts div a {
        height: max-content;
        flex: 1 1 120px;
    }

    .img-container-posts div a img {
        height: auto;
        width: 100%;
        aspect-ratio: 1 / 1;
        min-height: 263.55px;
    }

    .form-email {
        grid-template-columns: 2fr 1fr;
    }

    .form-email input:nth-child(2) {
        min-width: 46.8px;
    }

    .bottom-article-img div a {
        height: max-content;
    }

    .bottom-article-img div a img {
        height: auto;
    }

    .aspect-ratio1 {
        aspect-ratio: 2 / 3;
    }

    .grid-main {
        display: grid;
        grid-template-columns: 2fr 1fr;
        gap: 30px;
    }

    .first-article {
        grid-column: 1 / 3;
        grid-row: 1;
        max-width: 80%;
        margin: 30px auto;
    }

    .description-article {
        grid-column: 1 / 3;
        grid-row: 3;
        height: max-content;
        max-width: 80%;
        margin: 20px auto;
    }

    .description-div {
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin: 0 20px;
        gap: 40px;
    }

    .grid-large-size {
        grid-column: 1 / 3;
        grid-row: 4;
        margin: 0 auto;
        max-width: 80%;
    }

    .info-article {
        justify-content: center;
        align-items: center;
        flex-flow: row nowrap;
        margin: 0;
    }

    .div-info {
        grid-column: 1 / 3;
        grid-row: 5;
    }

    .customer-article {
        grid-column: 1 / 3;
        grid-row: 6;
        width: 90%;
        margin: 0 auto;
    }

    .customer-div {
        flex-flow: row nowrap;
    }

    .digital-menu-article {
        gap: 0;
        grid-column: 1 / 3;
        grid-row: 2;
        max-width: 1000px;
        margin: 30px auto;
    }

    /* teste */
    .digital-menu-article {
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
        margin-top: 20px;
        padding: 10px;
        gap: 10px;
        background-color: #fff;
        border-radius: 10px;
    }

    .digital-menu-article p, 
    .digital-menu-article h2 {
        color: var(--color-brown-palette);
    }

    .digital-menu-text {
        width: 100%;
        margin: 0;
    }

    .digital-menu-text h2 {
        font-size: 2em;
        
    }
    
    .digital-menu-text p {
        padding-top: 50px;
        font-size: 1.2em;
        line-height: 1.8em;
    }
    
    .digital-menu-text strong {
        color: #fff;
    }

    .menu-img img {
        aspect-ratio: 1 / 1;
        max-width: 400px;
    }
}`, "",{"version":3,"sources":["webpack://./frontend/assets/css/large-size.css"],"names":[],"mappings":"AAAA,gBAAgB;;AAEhB;;;;GAIG;;AAEH;IACI;QACI,iBAAiB;IACrB;;IAEA;QACI,eAAe;QACf,YAAY;QACZ,aAAa;QACb,gCAAgC;QAChC,8BAA8B;QAC9B,mBAAmB;QACnB,SAAS;IACb;;IAEA;QACI,sBAAsB;IAC1B;;IAEA;QACI,YAAY;IAChB;;IAEA;QACI,aAAa;QACb,mBAAmB;IACvB;;IAEA;QACI,UAAU;QACV,WAAW;IACf;;IAEA;QACI,mBAAmB;QACnB,aAAa;QACb,SAAS;IACb;;IAEA;QACI,oBAAoB;QACpB,kBAAkB;;QAElB,mBAAmB;IACvB;;;IAGA;QACI,qCAAqC;IACzC;;;IAGA;QACI,YAAY;IAChB;;IAEA;QACI,mBAAmB;QACnB,eAAe;IACnB;;IAEA;QACI,YAAY;QACZ,WAAW;QACX,mBAAmB;QACnB,oBAAoB;IACxB;;IAEA;QACI,8BAA8B;IAClC;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,mBAAmB;IACvB;;IAEA;QACI,YAAY;IAChB;;IAEA;QACI,mBAAmB;IACvB;;IAEA;QACI,aAAa;QACb,8BAA8B;QAC9B,SAAS;IACb;;IAEA;QACI,kBAAkB;QAClB,WAAW;QACX,cAAc;QACd,iBAAiB;IACrB;;IAEA;QACI,kBAAkB;QAClB,WAAW;QACX,mBAAmB;QACnB,cAAc;QACd,iBAAiB;IACrB;;IAEA;QACI,aAAa;QACb,8BAA8B;QAC9B,cAAc;QACd,SAAS;IACb;;IAEA;QACI,kBAAkB;QAClB,WAAW;QACX,cAAc;QACd,cAAc;IAClB;;IAEA;QACI,uBAAuB;QACvB,mBAAmB;QACnB,qBAAqB;QACrB,SAAS;IACb;;IAEA;QACI,kBAAkB;QAClB,WAAW;IACf;;IAEA;QACI,kBAAkB;QAClB,WAAW;QACX,UAAU;QACV,cAAc;IAClB;;IAEA;QACI,qBAAqB;IACzB;;IAEA;QACI,MAAM;QACN,kBAAkB;QAClB,WAAW;QACX,iBAAiB;QACjB,iBAAiB;IACrB;;IAEA,UAAU;IACV;QACI,aAAa;QACb,wBAAwB;QACxB,mBAAmB;QACnB,gBAAgB;QAChB,aAAa;QACb,SAAS;QACT,sBAAsB;QACtB,mBAAmB;IACvB;;IAEA;;QAEI,iCAAiC;IACrC;;IAEA;QACI,WAAW;QACX,SAAS;IACb;;IAEA;QACI,cAAc;;IAElB;;IAEA;QACI,iBAAiB;QACjB,gBAAgB;QAChB,kBAAkB;IACtB;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,mBAAmB;QACnB,gBAAgB;IACpB;AACJ","sourcesContent":["@charset \"utf-8\";\r\n\r\n/* @media (min-width: ) {\r\n    .img-container-posts {\r\n        grid-template-columns: repeat(3, 1fr) !important; \r\n    }\r\n} */\r\n\r\n@media (min-width: 905px) {\r\n    .center-margin {\r\n        max-width: 1500px;\r\n    }\r\n\r\n    .mid-bar {\r\n        margin-top: 0px;\r\n        height: 70px;\r\n        display: flex;\r\n        flex-flow: row nowrap !important;\r\n        justify-content: s