___CSS_LOADER_EXPORT___.push([module.id, `.side-bar {
    display: flex;
    justify-content: start;
    align-items: center;
    flex-flow: column nowrap;
    gap: 10px;
    width: 0;
    height: 100%;
    position: fixed;
    padding-top: 30px;
    left: 0px;
    transition: all 1s;
    z-index: 99;
    background-color: #fff;
    overflow: hidden;
    /* animation: grow .4s linear;  */
    box-shadow: 2px 0 4px rgba(0, 0, 0, 0.4);
}

.open-side-bar {
    padding: 30px 5px 5px;
    margin: auto 0;
    width: 200px;
}

.side-products-list {
    display: none;
    line-height: 1.5em;
    margin: 10px 0 0px 0px;
}

.side-products-list li {
    list-style: none;
    font-size: 0.8em;
}

.side-options-menu {
    display: flex;
    flex-flow: column wrap;
    height: max-content;
    justify-content: space-around;
}

.side-options-menu li,
.close-menu a {
    font-family: var(--font-dm-mono-display);
    text-transform: uppercase;
    margin-top: 20px;
}

.close-menu {
    z-index: 9999;
    display: flex;
    justify-content: end;
    width: 100%;
}

.close-symbol {
    cursor: pointer;
}

.close-symbol:hover {
    transition: all .5s;
    transform: rotate(360deg);
}

.opacity-body {
    opacity: .5;
}

.opacity-side-bar {
    opacity: 1;
}

.open-dropdown-menu {
    display: block;
}

.up-arrow {
    transform: rotateX(180deg);
    transition: all .2s;
}`, "",{"version":3,"sources":["webpack://./frontend/assets/css/nav-side.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,wBAAwB;IACxB,SAAS;IACT,QAAQ;IACR,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,SAAS;IACT,kBAAkB;IAClB,WAAW;IACX,sBAAsB;IACtB,gBAAgB;IAChB,iCAAiC;IACjC,wCAAwC;AAC5C;;AAEA;IACI,qBAAqB;IACrB,cAAc;IACd,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA;IACI,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,6BAA6B;AACjC;;AAEA;;IAEI,wCAAwC;IACxC,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,aAAa;IACb,oBAAoB;IACpB,WAAW;AACf;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,mBAAmB;IACnB,yBAAyB;AAC7B;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,0BAA0B;IAC1B,mBAAmB;AACvB","sourcesContent":[".side-bar {\r\n    display: flex;\r\n    justify-content: start;\r\n    align-items: center;\r\n    flex-flow: column nowrap;\r\n    gap: 10px;\r\n    width: 0;\r\n    height: 100%;\r\n    position: fixed;\r\n    padding-top: 30px;\r\n    left: 0px;\r\n    transition: all 1s;\r\n    z-index: 99;\r\n    background-color: #fff;\r\n    overflow: hidden;\r\n    /* animation: grow .4s linear;  */\r\n    box-shadow: 2px 0 4px rgba(0, 0, 0, 0.4);\r\n}\r\n\r\n.open-side-bar {\r\n    padding: 30px 5px 5px;\r\n    margin: auto 0;\r\n    width: 200px;\r\n}\r\n\r\n.side-products-list {\r\n    display: none;\r\n    line-height: 1.5em;\r\n    margin: 10px 0 0px 0px;\r\n}\r\n\r\n.side-products-list li {\r\n    list-style: none;\r\n    font-size: 0.8em;\r\n}\r\n\r\n.side-options-menu {\r\n    display: flex;\r\n    flex-flow: column wrap;\r\n    height: max-content;\r\n    justify-content: space-around;\r\n}\r\n\r\n.side-options-menu li,\r\n.close-menu a {\r\n    font-family: var(--font-dm-mono-display);\r\n    text-transform: uppercase;\r\n    margin-top: 20px;\r\n}\r\n\r\n.close-menu {\r\n    z-index: 9999;\r\n    display: flex;\r\n    justify-content: end;\r\n    width: 100%;\r\n}\r\n\r\n.close-symbol {\r\n    cursor: poi